nav{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #f6f6f6;
    padding-bottom: 10%;
  
      h1{
        justify-content: space-between;
        text-align: center;
        padding-left: 20%;
        padding-right: 20%;
        margin-bottom: 0%;
        margin-top: 10%;
        font-weight: 700;      
      }
      p{
        text-align: center;
        padding-left: 10%;
        padding-right: 10%;
        margin-top: 3%;
        line-height: 21px;
        font-weight: 300;
        font-size: 18px;
      }
  }
  
  .title button{
    border: none;
    border-radius: 30px;
    background: linear-gradient(#9356DC, #FF79DA);
    height: 50px;
    width: 220px;
    color: #ffffff;
    margin: 4%;
    font-weight: 500;
    font-size: 16px;
    transition-property: transform;
    transition-duration: 1000ms;
    &:hover {
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.15)), linear-gradient(200.64deg, #9356DC -5.2%, #FF79DA 110.74%);
      box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.35);
      border-radius: 25px;
      transform: scale(1.01) ;
      cursor: pointer;
    }
  }